section.header {
    color: rgb(49, 67, 81);
    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .logo {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        cursor: pointer;
        h1 {
            font-family: monospace;
            font-size: 42px;
            font-weight: 500;
            color: rgb(49, 67, 81);
        }

        span{
            color: gray;
            font-size: 1rem;
            font-style: italic;
        }

        @media (max-width: $mobile-breakpoint) {
            justify-content: center;
            align-items: center;
            background-color: var(--primary-color);
            color: white;
            img {
                display: none;
            }

            h1 {
                font-size: 20px;
                padding: 3px;
                color: white;
            }
        }
    }

    nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        a {
            margin-left: 20px;
        }
    }



    // background-color: var(--primary-color);
    // background-image: linear-gradient(315deg, var(--primary-color-gradient-end) 0%, var(--primary-color) 74%);
    // background: rgba(0, 0, 0, 0.058823529411764705);
}
